<template>
	<section class="card">
		<mt-swipe :auto='4000'>
		  	<mt-swipe-item v-for="item in card_listUrl" :key="item.id">
		  		<img :src="item.url"/>
		  	</mt-swipe-item>
		</mt-swipe>
		<div class="card_list">
			<div @click="skip(index)" class="bank" v-for="(item,index) in card_list1">
				<span class="one">
					<img :src="item.logoUrl">
				</span>
				<span>{{ item.bankName }}</span>
			</div>
		</div>
		<div class="progress">
			<div></div>
			<span>办卡进度</span>
		</div>
		<ul class="select_progress">
			<li v-for="(item,index) in card_list" @click="skipTwo(index)">
				<img :src="item.logoUrl">
				<div class="msg">
					<span class="title">{{ item.bankName }}</span>
					<span class="detail_msg"> {{ item.message }} </span>
				</div>
			</li>
		</ul>
	</section>
  
</template>
<script>
import { list, recode } from './../api/api'
import { mapMutations } from 'vuex'
import { getStore } from '../config/mUtils'
export default {
  	data () {
    	return {
    		card_list: [],
    		card_list1: [],
    		card_listUrl: [{
    			url: require('../images/banner_card.png')
    		}, {
    			url: require('../images/banner_card_01.png')
    		}, {
    			url: require('../images/banner_card_02.png')
    		}]
    	}
  	},
  	methods: {
  		...mapMutations([
			'TYPE_DHH'
		]),
    	init() {
    		this.TYPE_DHH('3')
    		let params = {
    			showType: 2
    		}
    		list(params).then(res => {
  				if (res.code === 200) {
  					this.card_list = res.data
   				}
   				let params = {
   					showType: 1
   				}
   				list(params).then(res => {
   					if (res.code === 200) {
   						this.card_list1 = res.data
   					}
   				})
  			})
    	},
    	skip(index) {
    		let params = {
				userId: getStore('userId'),
				positionMark: 'card',
				flag: this.card_list1[index].id
			}
			recode(params).then(res => {
				if (res.code === 200) {
					window.location.href = this.card_list1[index].skipUrl;
				}
			})
    	},
    	skipTwo(index) {
    		let params = {
				userId: getStore('userId'),
				positionMark: 'card',
				flag: this.card_list[index].id
			}
			console.log(params)
			recode(params).then(res => {
				if (res.code === 200) {
					console.log(res.code)
					window.location.href = this.card_list[index].skipUrl;
				}
				console.log(res.msg)
				console.log(res.code)
			})
    	}
  	},
  	mounted() {
  		this.init()
  		// this.inits()
  	}
}
</script>

<style lang="scss" rel="stylesheet/scss">
@import './../common/sass/mixin.scss';
	.card {
		background: #f3f2f3;
		width: 100%;
		height: 100%;
		position: absolute;
		z-index: 10;
		.mint-swipe {
			width: 100%;
			height: 4.6rem;
		}
		.mint-swipe-items-wrap {
			width: 100%;
			height: 100%;

			img {
					width: 100%;
					height: 100%;
				}
		}
		.card_list {
			width: 100%;
			height: 1.8rem;
			border-top: 0.01rem solid #ddd;
			border-bottom: 0.01rem solid #ddd;
			background: #fff;
			margin-top: 0.3rem;
			display: flex;
			display: -webkit-flex;
			display: -moz-flex;
			display: -ms-flex;
			text-align: center;

			.bank {
				flex :1;
				-webkit-flex :1;
				-moz-flex: 1;
				-ms-flex: 1;

				.one{
					margin: 0.37rem 0 0.26rem 0;
					height: 0.7rem;
				}
				span {
					display: inline-block;
					width: 100%;
					color: #333;
					font-size: 0.2rem;
					img {
						width: 0.7rem;
						height: 0.7rem;
					}
				}
			}
		}
		.progress {
			height: 0.7rem;
			line-height: 0.7rem;
			position: relative;

			div {
				position: absolute;
				width: 0.05rem;
				height: 0.3rem;
				background: #d84949;
				top: 0.22rem;
				left: 0.2rem;
			}
			span {
				position: absolute;
				left: 0.2rem;
				font-size: 0.25rem;
				margin-left: 0.2rem;
				font-weight: 600;
			}
		}
		.select_progress {
			overflow: hidden;
			background: #f2f3f2;
			li {
				float: left;
				background: #fff;
				border-bottom: 0.01rem solid #ddd;	
				height: 1.5rem;
				box-sizing: border-box;
				width: 50%;
				position: relative;
				img {
					width: 0.8rem;
					position: absolute;
					top: 50%;
					left: 0.2rem;
					transform: translateY(-50%);
					margin-left: 0.15rem;
				}
				.msg {
					// margin-left: 0.15rem;
					// margin-top: 0.43rem;
					display: inline-block;
					position: absolute;
					left: 1.5rem;
					top: 0.4rem;
					span {
						display: block;
					}
					.title {
						
						font-size: 0.27rem;
						margin-bottom: 0.16rem;
					}
					.detail_msg {
						font-size: 0.2rem;
						color: #b2b2b2;
					}
				}
			}
			li:nth-child(1) {
				border-top: 0.01rem solid #ddd;
			}
			li:nth-child(2) {
				border-top: 0.01rem solid #ddd;
			}
			li:nth-child(Odd) {
				border-right: 0.01rem solid #ddd;
			}
			li:last-child {
				margin-bottom: 1.1rem;
			}
		}
	}
</style>



